<script>
import { mapActions, mapGetters } from "vuex";
export default {
  name: "aftersaleFactoryjudgementWExamine",
  data() {
    return {
      authority: {},
      TableData: null,
      loading: true,
      params: {}
    };
  },
  computed: {
    ...mapGetters([
      "factoryjudgementExamine_Get",
      "factoryjudgementDefault_Get"
    ])
  },
  methods: {
    ...mapActions(["factoryjudgementDefault"]),
    goBack() {
      this.$router.push({
        path: "/aftersale/factoryjudgement"
      });
      // wz annotation cache
      // this.factoryjudgementDefault({
      //   // moduleName: "factoryjudgementDefault",
      //   // params: this.factoryjudgementExamine_Get.params,
      //   params: this.factoryjudgementExamine_Get? this.factoryjudgementDefault_Get.params : {},
      //   goPage: this.factoryjudgementExamine_Get.goPage,
      //   pageSize: this.factoryjudgementExamine_Get.pageSize
      // });
    },
    audit(api) {
      this.$axios({
        method: "post",
        url: this.$api[api],
        data: {
          factory_appraisal_id: this.factoryjudgementExamine_Get
            .factory_appraisal_id
        }
      }).then(res => {
        if (res.data.code == 200) {
          this.$message({
            message: res.data.data,
            type: "success"
          });
          this.goBack();
        }
      });
    }
  },
  mounted() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    this.$axMethod
      .getCompanyAppraisalInfo({
        factory_appraisal_id: this.factoryjudgementExamine_Get
          .factory_appraisal_id
      })
      .then(res => {
        if (res.data.code == 200) {
          this.loading = false;
          this.params = res.data.data.infoList;
          this.TableData = res.data.data.goodsList;
        }
      });
  }
};
</script>

